@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  height: 100%;
  box-sizing: border-box; }

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 12px;
  font-weight: 400;
  color: #232323;
  background-color: #F7F7F7; }

main {
  flex-grow: 1; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.executive-desk {
  display: flex;
  height: 576px;
  margin-top: 80px; }

.executive-desk__sidebar {
  width: 100px;
  background-color: #393A3F; }

.executive-desk__content {
  width: 978px;
  padding: 30px 20px;
  background-color: #FFFFFF; }
  .executive-desk__content h1 {
    margin: 0;
    margin-bottom: 20px;
    padding: 0 16px;
    font-size: 32px;
    line-height: 40px;
    font-weight: 400;
    color: #000000; }

.executive-desk__tabs {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 22px;
  padding: 6px 16px;
  background-color: #F7F7F7; }
  .executive-desk__tabs a {
    min-width: 153px;
    margin-right: 20px;
    padding: 17px 36px 16px 36px;
    text-align: center;
    color: #556080;
    background-color: #FFFFFF;
    border-radius: 2px; }
    .executive-desk__tabs a:nth-child(2) {
      min-width: 168px; }
    .executive-desk__tabs a.active {
      color: #CACACA;
      background-color: #556080; }

.vacation-schedule h2 {
  margin: 0;
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  color: #2D2E2E; }

.vacation-schedule__header {
  display: flex;
  justify-content: space-between;
  padding: 17px 0 17px 20px;
  background-color: #F7F7F7; }

.vacation-schedule__year-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 107px;
  font-size: 18px; }
  .vacation-schedule__year-navigation button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    padding: 0;
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer; }

.vacation-schedule__year-btn-prev svg {
  transform: rotate(180deg); }

.vacation-schedule__table-titles {
  display: flex;
  padding-top: 4px;
  padding-bottom: 8px;
  font-size: 10px;
  text-align: center;
  background-color: #F7F7F7; }
  .vacation-schedule__table-titles span {
    flex-grow: 1; }
    .vacation-schedule__table-titles span:nth-child(1) {
      flex-grow: 0;
      width: 111px; }
    .vacation-schedule__table-titles span:nth-child(2) {
      flex-grow: 0;
      width: 49px;
      margin-right: 9px; }

.vacation-schedule__staff {
  font-size: 12px; }

.vacation-schedule__employee {
  display: flex;
  align-items: center;
  min-height: 49px;
  border-bottom: 1px solid #F7F7F7; }

.vacation-schedule__employee-personal-info {
  display: flex;
  align-items: center;
  width: 111px; }

.vacation-schedule__employee-photo {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  margin-right: 8px; }

.vacation-schedule__days {
  width: 49px;
  margin-right: 9px;
  text-align: center; }

.vacation-schedule__month {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
  height: 21px;
  flex-grow: 1; }

.vacation-schedule__day {
  width: 8px;
  height: 3px;
  margin-right: 2px; }

.popup {
  position: absolute;
  width: 345px;
  height: 146px;
  padding: 16px;
  background-color: #FFFFFF;
  box-shadow: 0 0 4px 0 rgba(107, 107, 107, 0.5);
  border-radius: 4px; }

.popup__employee-personal-info {
  display: flex;
  margin-bottom: 15px; }

.popup__employee-name {
  max-width: 130px;
  font-size: 14px;
  line-height: 19px;
  font-weight: 700;
  color: #000000; }

.popup__employee-photo {
  margin-right: 17px; }

.popup__vacation-days {
  margin: 0;
  padding-left: 20px;
  line-height: 19px;
  color: #000000; }

.popup__vacation-type {
  padding-left: 20px;
  line-height: 19px;
  color: #AEAEAE; }

.popup__vacation-color {
  position: absolute;
  bottom: 42px;
  left: 16px;
  width: 12px;
  height: 16px;
  background-color: #1EBEB4; }
